import "./App.scss";
import React from "react";
import { SketchField, Tools } from "react-sketch";
import { SketchPicker } from "react-color";

import {
	Popup,
	Button,
	Icon,
	Form,
	Label,
	Grid,
	Reveal,
	Image,
	Dropdown,
} from "semantic-ui-react";

import { FiMove, FiZoomIn, FiZoomOut } from "react-icons/fi";
import { FaPencilAlt, FaRegCircle, FaRegSquare } from "react-icons/fa";
import { AiOutlineArrowsAlt } from "react-icons/ai";
import { GiArrowCursor } from "react-icons/gi";
import { GoPencil } from "react-icons/go";
import { BsCircleFill, BsSquareFill, BsImageFill } from "react-icons/bs";
import { MdRedo, MdUndo } from "react-icons/md";

import logo from "../assets/imgs/binary-crystal-logo.png";

class App extends React.Component {
	state = {
		bg: null,
		tool: Tools.Pencil,
		col: "lineCol",
		lineCol: "#000",
		fillCol: "#36EAB7",
		isFill: false,
		lineWidth: 3,
		openLW: false,
		bgCol: "transparent",
		canUndo: false,
		canRedo: false,
		cselect: "img",
		///Text
		text: "Shivam",
		textAlign: "left",
		textFill: "#000",
		isTextBack: false,
		textBack: "#e34",
		isStroke: false,
		strokeCol: "purple",
		strokeW: 1,
		textOpaticy: 100,
		fontFamily: "Verdana",
		// Image
		imgUrl:
			"https://www.google.com/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png",
		photoU: null,
		photos: [],
	};

	setTool = (tool, fill = false) => {
		this.setState({ tool: tool, isFill: fill });
	};

	onColorChange = (color) => {
		this.setState({ [this.state.col]: color.hex });
	};

	_undo = () => {
		this._sketch.undo();
		this.setState({
			canUndo: this._sketch.canUndo(),
			canRedo: this._sketch.canRedo(),
		});
	};

	_redo = () => {
		this._sketch.redo();
		this.setState({
			canUndo: this._sketch.canUndo(),
			canRedo: this._sketch.canRedo(),
		});
	};

	_onSketchChange = () => {
		let prev = this.state.canUndo;
		let now = this._sketch.canUndo();
		if (prev !== now) {
			this.setState({ canUndo: now });
		}
	};

	// http://fabricjs.com/docs/fabric.IText.html
	_addText = () => {
		const {
			textFill,
			isTextBack,
			textBack,
			isStroke,
			strokeCol,
			strokeW,
			textAlign,
			textOpaticy,
		} = this.state;
		this._sketch.addText(this.state.text, {
			fill: textFill,
			backgroundColor: isTextBack ? textBack : "transparent",
			strokeWidth: isStroke ? strokeW : 0,
			stroke: strokeCol,
			textAlign: textAlign,
			opacity: textOpaticy / 100,
			paintFirst: "stroke",
			fontFamily: this.state.fontFamily,
		});
		this.setState({ tool: Tools.Select, cselect: "text" });
	};

	_addImg = (photo) => {
		// let canvas = this._sketch._fc;
		const img = this.getImage(photo);
		this._sketch.addImg(img, {
			strokeWidth: 50,
			stroke: "red",
			// paintFirst: "stroke",
		});
		this.setState({ tool: Tools.Select });
	};

	onFile = (eve) => {
		const photos = this.state.photos;
		photos.push(...eve.target.files);
		this.setState({ photos: photos });
		// this.setState({
		// 	[eve.target.name]: eve.target.files[0],
		// });
	};
	getImage = (photo) => {
		if (typeof photo === "string") {
			return photo;
		} else if (typeof photo === "object" && photo !== null) {
			return URL.createObjectURL(photo);
		}
		return null;
	};

	renderImages = () => {
		return this.state.photos.map((photo) => {
			return (
				<Grid.Column>
					<Reveal animated="move up" instant>
						<Reveal.Content visible>
							<Image src={this.getImage(photo)} fluid />
						</Reveal.Content>
						<Reveal.Content hidden>
							<div
								style={{
									width: "100%",
									height: "100%",
									backgroundColor: "yellow",
								}}
							>
								<Button
									primary
									fluid
									onClick={() => this._addImg(photo)}
								>
									Add
								</Button>
							</div>
						</Reveal.Content>
					</Reveal>
					{/* <img style={{ width: "100%" }} src={this.getImage(photo)} /> */}
				</Grid.Column>
			);
		});
	};

	render() {
		return (
			<div className="mainWrapper">
				<div className="leftWrapper">
					{/* <div className="logoWrapper">
						<img src={logo} className="logo" />
					</div> */}
					<div className="leftpbox">
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Pencil
									? "active"
									: null
							}`}
							onClick={() => this.setTool(Tools.Pencil)}
						>
							<GoPencil />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Line ? "active" : null
							}`}
							onClick={() => this.setTool(Tools.Line)}
						>
							<AiOutlineArrowsAlt />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Circle &&
								!this.state.isFill
									? "active"
									: null
							}`}
							onClick={() => this.setTool(Tools.Circle)}
						>
							<FaRegCircle />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Rectangle &&
								!this.state.isFill
									? "active"
									: null
							}`}
							onClick={() => this.setTool(Tools.Rectangle)}
						>
							<FaRegSquare />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Circle &&
								this.state.isFill
									? "active"
									: null
							}`}
							onClick={() => this.setTool(Tools.Circle, true)}
						>
							<BsCircleFill />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Rectangle &&
								this.state.isFill
									? "active"
									: null
							}`}
							onClick={() => this.setTool(Tools.Rectangle, true)}
						>
							<BsSquareFill />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Pan ? "active" : null
							}`}
							onClick={() => this.setTool(Tools.Pan)}
						>
							<FiMove />
						</div>
						<div
							className={`sqBtn ${
								this.state.tool === Tools.Select
									? "active"
									: null
							}`}
							onClick={() => this.setTool(Tools.Select)}
						>
							<GiArrowCursor />
						</div>
						<div
							className={`sqBtn ${
								!this.state.canUndo ? "disable" : null
							}`}
							onClick={this._undo}
						>
							<MdUndo />
						</div>
						<div
							className={`sqBtn ${
								!this.state.canRedo ? "disable" : null
							}`}
							onClick={this._redo}
						>
							<MdRedo />
						</div>
						<div
							className="sqBtn"
							onClick={() => this._sketch.zoom(1.25)}
						>
							<FiZoomIn />
						</div>
						<div
							className="sqBtn"
							onClick={() => this._sketch.zoom(0.8)}
						>
							<FiZoomOut />
						</div>
						<div
							className={`sqBtn ${
								this.state.cselect === "text" ? "active" : null
							}`}
							onClick={() => this.setState({ cselect: "text" })}
						>
							<b>T</b>
						</div>
						<div
							className={`sqBtn ${
								this.state.cselect === "img" ? "active" : null
							}`}
							onClick={() => {
								this.setState({ cselect: "img" });
							}}
						>
							<BsImageFill />
						</div>
						<Popup
							trigger={
								<Button
									className="sqtow"
									icon
									labelPosition="left"
									style={{
										backgroundColor: this.state.lineCol,
										color: "white",
									}}
								>
									Stroke
									<Icon name="tint" />
								</Button>
							}
							eventsEnabled={true}
							on="click"
						>
							<SketchPicker
								color={this.state.lineCol}
								onChangeComplete={(col) =>
									this.setState({ lineCol: col.hex })
								}
							/>
						</Popup>
						<Popup
							trigger={
								<Button
									className="sqtow"
									icon
									labelPosition="left"
									style={{
										backgroundColor: this.state.fillCol,
										color: "white",
									}}
								>
									Fill
									<Icon name="tint" />
								</Button>
							}
							eventsEnabled={true}
							on="click"
						>
							<SketchPicker
								color={this.state.fillCol}
								onChangeComplete={(col) =>
									this.setState({ fillCol: col.hex })
								}
							/>
						</Popup>
						{/* <p
							className="sqtow"
							style={{ fontSize: "1rem", marginBottom: "-10px" }}
						>
							Fill Color
						</p>
						<div
							className={`sqBtn sqtow ${
								this.state.col === "fillCol" ? "active" : null
							}`}
							onClick={() => this.setTool(Tools.Select)}
						>
							<GiArrowCursor />
						</div> */}
					</div>
				</div>
				<div className="centerWrapper">
					<SketchField
						ref={(c) => (this._sketch = c)}
						height="100vh"
						tool={this.state.tool}
						lineColor={this.state.lineCol}
						lineWidth={this.state.lineWidth}
						fillColor={
							this.state.isFill
								? this.state.fillCol
								: "transparent"
						}
						onChange={this._onSketchChange}
					/>
				</div>
				<div className="rightWrapper">
					<div className="rpbox">
						{this.state.cselect === "col" ? (
							<SketchPicker
								color={this.state[this.state.col]}
								onChange={this.onColorChange}
							/>
						) : null}
						{this.state.cselect === "text" ? (
							<div className="textP">
								<h2>Insert Text</h2>
								<Form>
									<Form.TextArea
										placeholder="Text"
										rows={4}
										fluid
										value={this.state.text}
										onChange={(eve) =>
											this.setState({
												text: eve.target.value,
											})
										}
									/>
								</Form>

								<div style={{ height: "10px" }} />
								<Popup
									trigger={
										<Label
											as="a"
											style={{
												backgroundColor: this.state
													.textFill,
												color: "white",
											}}
										>
											<Icon name="tint" />
											Text Color
										</Label>
									}
									eventsEnabled={true}
									on="click"
								>
									<SketchPicker
										color={this.state.textFill}
										onChangeComplete={(col) =>
											this.setState({ textFill: col.hex })
										}
									/>
								</Popup>
								<div style={{ height: "10px" }} />
								<Form>
									<Form.Checkbox
										toggle
										label="Text Background"
										checked={this.state.isTextBack}
										onChange={() =>
											this.setState({
												isTextBack: !this.state
													.isTextBack,
											})
										}
									/>
								</Form>
								<div style={{ height: "10px" }} />
								{this.state.isTextBack ? (
									<Popup
										trigger={
											<Label
												as="a"
												style={{
													backgroundColor: this.state
														.textBack,
													color: "white",
												}}
											>
												<Icon name="tint" />
												Background Color
											</Label>
										}
										eventsEnabled={true}
										on="click"
									>
										<SketchPicker
											color={this.state.textBack}
											onChangeComplete={(col) =>
												this.setState({
													textBack: col.hex,
												})
											}
										/>
									</Popup>
								) : null}
								<div style={{ height: "10px" }} />
								<Form>
									<Form.Checkbox
										toggle
										label="Stroke"
										checked={this.state.isStroke}
										onChange={() =>
											this.setState({
												isStroke: !this.state.isStroke,
											})
										}
									/>
								</Form>
								<div style={{ height: "10px" }} />
								{this.state.isStroke ? (
									<Popup
										trigger={
											<Label
												as="a"
												style={{
													backgroundColor: this.state
														.strokeCol,
													color: "white",
												}}
											>
												<Icon name="tint" />
												Stroke Color
											</Label>
										}
										eventsEnabled={true}
										on="click"
									>
										<SketchPicker
											color={this.state.strokeCol}
											onChangeComplete={(col) =>
												this.setState({
													strokeCol: col.hex,
												})
											}
										/>
									</Popup>
								) : null}
								{this.state.isStroke ? (
									<Form>
										<div style={{ height: "10px" }} />
										<Form.Input
											label="Stroke Width"
											type="range"
											min={0}
											max={12}
											value={this.state.strokeW}
											onChange={(eve) =>
												this.setState({
													strokeW: eve.target.value,
												})
											}
										/>
									</Form>
								) : null}
								<div style={{ height: "10px" }} />
								<Form>
									<Form.Dropdown
										selection
										label="Text Align"
										value={this.state.textAlign}
										onChange={(e, { value }) =>
											this.setState({ textAlign: value })
										}
										options={[
											{
												key: "left",
												value: "left",
												text: "Left",
											},
											{
												key: "right",
												value: "right",
												text: "Right",
											},
											{
												key: "center",
												value: "center",
												text: "Center",
											},
										]}
									/>
								</Form>
								<div style={{ height: "10px" }} />
								<Form>
									<Form.Dropdown
										selection
										label="Font Family"
										text={this.state.fontFamily}
									>
										<Dropdown.Menu>
											{[
												"Verdana",
												"Lucida Console",
												"Arial",
												"Georgia",
												"Times New Roman",
												"Trebuchet MS",
											].map((item) => (
												<Dropdown.Item
													onClick={() =>
														this.setState({
															fontFamily: item,
														})
													}
												>
													<span
														style={{
															fontFamily: item,
														}}
													>
														{item}
													</span>
												</Dropdown.Item>
											))}
										</Dropdown.Menu>
									</Form.Dropdown>
								</Form>
								<div style={{ height: "10px" }} />
								<Form>
									<Form.Input
										label="Opacity"
										type="range"
										min={0}
										max={100}
										value={this.state.textOpaticy}
										onChange={(eve) =>
											this.setState({
												textOpaticy: eve.target.value,
											})
										}
									/>
								</Form>
								<div style={{ height: "10px" }} />
								<Button positive fluid onClick={this._addText}>
									Add Text
								</Button>
								<div style={{ height: "30px" }} />
							</div>
						) : null}
						{this.state.cselect === "img" ? (
							<div className="textP">
								<h2>Insert Image</h2>
								<Button
									icon
									color="teal"
									labelPosition="left"
									onClick={(eve) => {
										eve.preventDefault();
										document
											.getElementById("imgDDD")
											.click();
									}}
								>
									Upload Image
									<Icon name="upload" />
								</Button>
								<input
									type="file"
									multiple
									name="photoU"
									onChange={this.onFile}
									id="imgDDD"
									hidden
								/>
								<div style={{ height: "20px" }} />
								<Grid columns={2}>{this.renderImages()}</Grid>
								<div style={{ height: "10px" }} />
								{/* <Button positive fluid onClick={this._addImg}>
									Add Image
								</Button> */}
							</div>
						) : null}
					</div>
				</div>
			</div>
		);
	}
}

export default App;
